.container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 25px;
}
.input{
  padding: 15px;
  width: 80%;
  margin: auto;
  border: 2px solid lightgrey;
  border-radius: 10px;
  font-size: 16px;
}
.weather-data{
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center; 
  color: white;
}
.city{
  font-size: 50px;
  font-weight: 200px;

}
.temp{
  font-size: 90px;
  padding: 10px;
  border: 1px solid lightgrey;
}
.weather{
  font-size: 50px;
  font-weight: 200;
}